.app-ui-mask-disable-scroll {
	overflow-y: hidden;
}

/* Drawer */
.app-layout-drawer {
	position: fixed;
	top: 0;
	bottom: 0;
	z-index: 5;
	display: block;
	width: 240px;
	background-color: #fff;
	border-right: 1px solid #eeeeee;
	font-weight: 500;
	background-color: #fff;
	visibility: hidden;
	transform: translateX(-240px);
	transform-style: preserve-3d;
	transition: 0.2s cubic-bezier(0.4, 0, 0.2, 1) 0.1s;
	transition-property: transform, visibility;
}

@media screen and (max-width: 991px) {
	.app-layout-drawer {
		-webkit-overflow-scrolling: touch;
		overflow-x: hidden;
		overflow-y: auto;
	}
}

.app-layout-drawer.app-ui-layout-drawer-open {
	transform: translateX(0);
}

.app-layout-drawer.app-ui-layout-drawer-visible {
	visibility: visible;
}

.drawer-header {
	position: relative;
	overflow: hidden;
	width: 100%;
	height: 64px;
	line-height: 64px;
	line-height: 1;
}

.drawer-header:before, .drawer-header:after {
	content: " ";
	display: table;
}

.drawer-header:after {
	clear: both;
}

.drawer-header a {
	display: block;
	height: auto;
	width: 100%;
}

.drawer-header a img {
	height: 64px;
	width: auto;
	max-width: none;
}

.drawer-main {
	margin-top: 24px;
	font-size: 13px;
	transform: translateZ(0);
}

/* Drawer nav menu */
.nav-drawer li a {
	padding-right: 24px;
	padding-left: 52.99999px;
	color: inherit;
	font-weight: 500;
}

.nav-drawer > li > a {
	border-left: 3px solid transparent;
	padding-top: 14px;
	padding-bottom: 13px;
}

.nav-drawer > .active > a {
	background-color: transparent;
	border-color: #7dc855;
}

.nav-drawer > .active > a:hover, .nav-drawer > .active > a:focus, .nav-drawer > .active > a:active {
	border-color: #7dc855;
}

/* Nav icon */
.nav-drawer > li > a > i {
	position: absolute;
	left: 21px;
	font-size: 1.25em;
	line-height: 1.25;
}

.nav-drawer-header {
	padding: 15px 24px 5px;
	font-size: 87.5%;
	letter-spacing: .5px;
	text-transform: uppercase;
	opacity: .38;
}

.nav-drawer-header:first-of-type {
	padding-top: 0;
}

/* Drawer nav-item with subnav */
.nav-item-has-subnav > a:after {
	position: absolute;
	top: 50%;
	right: 24px;
	font-family: Ionicons;
	font-size: 17px;
	content: '\f489';
	transform: translate(50%, -50%);
}

.nav-item-has-subnav.open > a:after {
	content: '\f462';
}

/* Drawer subnav */
.nav-subnav {
	display: none;
	margin-top: 8px;
	margin-bottom: 8px;
}

.nav-item-has-subnav.open > .nav-subnav {
	display: block;
}

.nav-subnav > li > a {
	padding-left: 55.99999px;
	padding-right: 24px;
	color: #838383;
}

.nav-subnav > li > a:hover, .nav-subnav > li > a:focus, .nav-subnav > li > a:active {
	color: inherit;
	background-color: transparent;
}

.nav-subnav > .active > a {
	color: inherit;
}

.drawer-footer {
	bottom: 0;
	width: 100%;
	height: 96px;
	margin-top: 24px;
	padding-top: 24px;
	padding-right: 24px;
	padding-bottom: 24px;
	padding-left: 24px;
	font-size: 13px;
	line-height: 24px;
}

.drawer-footer .copyright {
	margin-bottom: 0;
}

/* Header */
.app-layout-header {
	z-index: 4;
	background-color: #fff;
	-webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.14);
	box-shadow: 0 1px 2px rgba(0, 0, 0, 0.14);
}

.navbar-toggle[data-action*="sidebar_toggle"] {
	margin-left: 15px;
}

.navbar-page-title {
	display: inline-block;
	margin-right: 20px;
	padding-top: 20px;
	padding-bottom: 20px;
	font-weight: 500;
}

.navbar-brand {
	padding: 0;
	max-height: 64px;
}

.navbar-brand img {
	height: inherit;
}

/* Navbar Search form */
@media screen and (max-width: 991px) {
	.navbar .app-search-form {
		margin-top: 0;
		margin-bottom: 0;
	}
}

.navbar .app-search-form .form-control,
.navbar .app-search-form .btn {
	background-color: #fff;
	border-color: #edeeef;
}

.navbar .app-search-form .form-group {
