}

/* Backend header toolbar */
.navbar-toolbar > li:not(.dropdown-profile) > a {
	padding-top: 17px;
	padding-bottom: 17px;
	padding-left: 8px;
	padding-right: 8px;
}

.navbar-toolbar > li > a i {
	display: inline-block;
	width: 30px;
	height: 30px;
	line-height: 30px;
	font-size: 1.5em;
	text-align: center;
}

.navbar-toolbar > li > a i + .badge {
	position: absolute;
	top: 35%;
	right: 0;
	transform: translateY(-50%);
}

.dropdown-profile a {
	padding-top: 8px !important;
	padding-bottom: 8px !important;
}

.dropdown-profile a span {
	vertical-align: middle;
}

/* Layout modal mask */
.app-ui-mask-modal {
	background-color: rgba(0, 0, 0, 0.5);
	height: 100%;
	left: 0;
	opacity: .1;
	top: 0;
	visibility: hidden;
	width: 100%;
	z-index: 4;
	position: fixed;
	transition: visibility 0 linear 0.4s, opacity 0.4s cubic-bezier(0.4, 0, 0.2, 1);
	transform: translateZ(0);
}

.app-ui-mask-modal.app-ui-mask-visible {
	opacity: 1;
	visibility: visible;
	transition-delay: 0;
}

/* Layout content */
.app-layout-content {
	height: 100%;
	width: 100%;
}

/* Layout footer */
.app-layout-footer {
	background-color: #fff;
}

/* Layout options */
/* Layout has boxed canvas */
.layout-has-boxed-canvas {
	background-color: #eee;
}

.layout-has-boxed-canvas .app-layout-canvas {
	margin: 0 auto;
	max-width: 1170px;
	background-color: #f9f9f9;
}

/* Layout has drawer */
@media screen and (min-width: 992px) {
	.layout-has-drawer .app-layout-header,
	.layout-has-drawer .app-layout-content,
	.layout-has-drawer .app-layout-footer {
		padding-left: 240px;
	}
}

@media screen and (min-width: 992px) {
	.layout-has-drawer .app-layout-drawer {
		visibility: visible;
		transform: translateX(0);
		transition: none;
	}
}

/* Layout has fixed header */
.layout-has-fixed-header .app-layout-header {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
}

.layout-has-fixed-header.layout-has-boxed-canvas .app-layout-header {
	right: auto;
	left: auto;
	width: 100%;
	max-width: 1170px;
}

.layout-has-fixed-header .app-layout-content {
	padding-top: 64px;
}

/* Page header */
.page-header h1 {
	margin-top: 0;
	margin-bottom: 6px;
}

.page-header p:last-of-type {
	margin-bottom: 0;
}

/* Page content */
.page-content {
	padding-top: 40px;
	padding-bottom: 40px;
}

/* App forms */
/* Floating labels forms */
.form-floating {
	position: relative;
}

/* Material design Labels */
.form-floating > label {
	position: absolute;
	top: 6px;
	left: 12px;
	font-size: 85%;
	-webkit-transform: translate(0, -24px);
	-ms-transform: translate(0, -24px);
	-o-transform: translate(0, -24px);
	transform: translate(0, -24px);
}

.form-floating > label {
	color: #8c9399;
	font-size: 81.25%;
	font-weight: 300;
	line-height: 2;
	cursor: text;
	-webkit-transition: all 0.15s ease-out;
	-o-transition: all 0.15s ease-out;
	transition: all 0.15s ease-out;
	-webkit-transform: translate(0, 0);
	-ms-transform: translate(0, 0);
	-o-transform: translate(0, 0);
	transform: translate(0, 0);
}

/* Floating Form controls and input addons */
.form-floating > .form-control {
	background-color: transparent;
	padding-top: 15px;
	padding-bottom: 2.4px;
}

.form-floating > .form-control:focus {
	background-color: transparent;
}

.form-floating > .form-control:focus + label {
	color: #434343;
}

.form-floating > .form-control:focus + label,
.form-floating.open > label {
	font-size: 75%;
	cursor: default;
	opacity: .5;
	-webkit-transform: translate(0, -25%);
	-ms-transform: translate(0, -25%);
	-o-transform: translate(0, -25%);
	transform: translate(0, -25%);
}

.form-floating .form-control[disabled],
.form-floating .form-control[readonly],
.form-floating fieldset[disabled] .form-control {
	border: 1px dashed #ccc;
	-webkit-box-shadow: none;
	box-shadow: none;
}

