/* Css Inputs */
.css-input {
	position: relative;
	display: inline-block;
	margin: 2px 0;
	font-weight: 400;
	cursor: pointer;
}

.css-input input {
	position: absolute;
	opacity: 0;
}

.css-input input + span {
	position: relative;
	display: inline-block;
	margin-top: -3px;
	margin-right: 3px;
	vertical-align: middle;
}

.css-input input + span:after {
	position: absolute;
	content: '';
}

/* CSS Checkboxes */
.css-checkbox {
	margin: 7px 0;
}

.css-checkbox input + span {
	width: 20px;
	height: 20px;
	background-color: #fff;
	border: 1px solid #eeeeee;
	border-radius: 3px;
}

.css-checkbox input + span:after {
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	font-family: 'Ionicons';
	font-size: 13px;
	color: #fff;
	line-height: 18px;
	content: '\f122';
	text-align: center;
}

.css-checkbox input:checked + span:after {
	color: #434343;
}

.css-checkbox:hover input + span {
	border-color: #dedede;
	box-shadow: inset 0 0 1px rgba(0, 0, 0, 0.14);
}

/* Css Checkbox Variations */
.css-checkbox-sm {
	margin: 9px 0 8px;
	font-size: 12px;
}

.css-checkbox-sm input + span {
	width: 16px;
	height: 16px;
}

.css-checkbox-sm input + span:after {
	font-size: 8px;
	line-height: 15px;
}

.css-checkbox-lg {
	margin: 3px 0;
}

.css-checkbox-lg input + span {
	width: 30px;
	height: 30px;
}

.css-checkbox-lg input + span:after {
	font-size: 20px;
	line-height: 30px;
}

.css-checkbox-rounded input + span {
	border-radius: 50%;
}

.css-checkbox-primary input:checked + span {
	background-color: #7dc855;
	border-color: #7dc855;
}

.css-checkbox-info input:checked + span {
	background-color: #5bc0de;
	border-color: #5bc0de;
}

.css-checkbox-success input:checked + span {
	background-color: #5cb85c;
	border-color: #5cb85c;
}

.css-checkbox-warning input:checked + span {
	background-color: #f0ad4e;
	border-color: #f0ad4e;
}

.css-checkbox-danger input:checked + span {
	background-color: #d9534f;
	border-color: #d9534f;
}

/* CSS Radios */
.css-radio {
	margin: 7px 0;
}

.css-radio input + span {
	width: 20px;
	height: 20px;
	background-color: #fff;
	border: 1px solid #eeeeee;
	border-radius: 50%;
}

.css-radio input + span:after {
	top: 2px;
	right: 2px;
	bottom: 2px;
	left: 3px;
	background-color: #fff;
	border-radius: 50%;
	opacity: 0;
	-webkit-transition: opacity 0.2s ease-out;
	-o-transition: opacity 0.2s ease-out;
	transition: opacity 0.2s ease-out;
}

.css-radio input:checked + span:after {
	opacity: 1;
}

.css-radio:hover input + span {
	border-color: #ccc;
}

/* CSS Radios Variations */
.css-radio-sm {
	margin: 9px 0 8px;
	font-size: 12px;
}

.css-radio-sm input + span {
	width: 16px;
	height: 16px;
}

.css-radio-lg {
	margin: 5px 0;
}

.css-radio-lg input + span {
	width: 26px;
	height: 26px;
}

.css-radio-default input:checked + span:after {
	background-color: #bdc6cf;
}

.css-radio-primary input:checked + span:after {
	background-color: #7dc855;
}

.css-radio-info input:checked + span:after {
	background-color: #5bc0de;
}

.css-radio-success input:checked + span:after {
	background-color: #5cb85c;
}

.css-radio-warning input:checked + span:after {
	background-color: #f0ad4e;
}

.css-radio-danger input:checked + span:after {
	background-color: #d9534f;
}

/* CSS Switches */
.switch {
	margin: 3px 0;
}

.switch input + span {
	width: 54px;
	height: 30px;
	background-color: rgba(0, 20, 35, 0.038);
	border-radius: 30px;
	-webkit-transition: background-color 0.4s;
	-o-transition: background-color 0.4s;
	transition: background-color 0.4s;
}

.switch input + span:after {
	top: 2px;
	bottom: 2px;
	left: 3px;
	width: 26px;
	background-color: #fff;
	border-radius: 50%;
