/* Base list */
.list {
	margin: 0;
	padding: 0;
	list-style: none;
}

.list > li {
	position: relative;
}

/* Activity list */
.list-activity > li {
	margin-bottom: 8px;
	padding-bottom: 8px;
	padding-left: 30px;
	font-size: 13px;
	border-bottom: 1px solid #eeeeee;
}

.list-activity > li > i:first-child {
