}

.list-events > li:hover {
	cursor: move;
}

/* Simple list */
.list-simple > li {
	margin-bottom: 24px;
}

.list-li-clearfix > li:before, .list-li-clearfix > li:after {
	content: " ";
	display: table;
}

.list-li-clearfix > li:after {
	clear: both;
}

/* Users list */
.list-users {
	margin: 0;
	padding: 0;
	list-style: none;
}

.list-users > li:last-child > a {
	border-bottom: none;
}

.list-users a {
	position: relative;
	padding: 12px 8px 12px 71px;
	color: inherit;
	display: block;
	min-height: 62px;
	font-weight: 500;
	border-bottom: 1px solid #eeeeee;
}

.list-users a:hover, .list-users a:focus {
	background-color: #f9f9f9;
}

.list-users a > img {
	position: absolute;
	left: 12px;
	top: 10px;
	width: 42px;
	height: 42px;
	border-radius: 50%;
}

.list-users a > i {
	position: absolute;
	left: 40px;
	top: 40px;
	display: inline-block;
	width: 18px;
	height: 18px;
	line-height: 18px;
	text-align: center;
	background-color: #fff;
	border-radius: 50%;
}

.list-users a .text-muted {
	font-weight: 300;
}

/* Styled ordered list */
.list-numbers {
	list-style: none;
	counter-reset: item;
}

.list-numbers:before {
	content: '';
	display: inline-block;
	position: absolute;
	border: 1px solid rgba(0, 20, 35, 0.07);
	left: 25px;
	height: 85%;
}

.list-numbers li {
	counter-increment: item;
}

.list-numbers li:before {
	content: counter(item);
	position: absolute;
	background-color: #f9f9f9;
	font-weight: 400;
	left: 10px;
	margin-top: -6.5px;
	display: inline-block;
	width: 1.875rem;
	height: 1.875rem;
	line-height: 1.875rem;
	text-align: center;
	border: 1px solid rgba(0, 20, 35, 0.07);
	border-radius: 50%;
}

.list-numbers.white li:before {
	background-color: #fff;
}

/* Card */
.card {
	margin-bottom: 24px;
	border-radius: 3px;
	-webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.14);
	box-shadow: 0 1px 2px rgba(0, 0, 0, 0.14);
}

.card:not([class*="bg-"]) {
	background-color: #fff;
}

/* Card links */
a.card {
	display: block;
	font-weight: inherit;
}

a.card:not(.bg-inverse) {
	color: inherit;
}

a.card:not(.bg-inverse):hover {
	color: inherit;
}

/* Card with table */
.card-table {
	width: 100%;
}

.card-table td {
	padding: 10px;
	vertical-align: middle;
}

/* Card header */
.card-header {
	display: table;
	width: 100%;
	margin: 0;
	padding: 24px 24px;
	border-bottom: 1px solid transparent;
	border-top-right-radius: 3px;
	border-top-left-radius: 3px;
}

.card-header > * {
	margin: 0;
	display: table-cell;
	vertical-align: middle;
}

.card.text-center .card-header > *,
.card-header.text-center > * {
	margin: 0 auto;
}

/* Card actions bar */
.card-actions {
	float: right;
	line-height: 1.1153;
	margin-bottom: 0;
	margin-left: auto;
	padding: 0;
}

.card-actions > li {
	display: inline-block;
	padding: 0;
}

.card-actions > li > a:not(.label),
.card-actions > li > button {
	color: #86939e;
	display: inline-block;
	padding: 0;
	line-height: 1;
	opacity: .7;
	vertical-align: middle;
	transition: opacity 0.15s ease-out;
}

.bg-inverse .card-actions > li > a:not(.label), .bg-inverse
.card-actions > li > button {
	color: #fff;
}

.card-actions > li > a:not(.label):hover,
.card-actions > li > button:hover {
	text-decoration: none;
	opacity: 1;
}

.card-actions > li > a:not(.label):active,
.card-actions > li > button:active {
	opacity: .6;
}

.card-actions > li > span {
	display: block;
}

.card.card-themed > .card-header .card-actions > li > span {
	color: #fff;
}

.card-actions > li > .label {
	line-height: 1.25;
}

.card-actions > li > a:focus {
	text-decoration: none;
	opacity: 1;
}

.card-actions > li > button {
	background: none;
	border: none;
}

.card-actions > li.active > a,
.card-actions > li.open > button {
	text-decoration: none;
	opacity: 1;
}

.card-actions > li + li {
	margin-left: 10px;
}

/* Card actions */
.card.card-act-refresh {
	position: relative;
}

.card.card-act-refresh:before {
	position: absolute;
	content: '';
	display: block;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background-color: rgba(255, 255, 255, 0.9);
	z-index: 1;
	-webkit-animation: fadeIn 0.2s ease-out;
	-o-animation: fadeIn 0.2s ease-out;
	animation: fadeIn 0.2s ease-out;
}

.card.card-act-refresh:after {
	position: absolute;
	top: 50%;
	left: 50%;
	margin: -20px 0 0 -20px;
	width: 40px;
	height: 40px;
	line-height: 40px;
	color: #434343;
	font-family: Ionicons;
	font-size: 18px;
	text-align: center;
	z-index: 2;
	content: "\f13d";
	-webkit-animation: fa-spin 2s infinite linear;
	-o-animation: fa-spin 2s infinite linear;
	animation: fa-spin 2s infinite linear;
}

.ie9 .card.card-act-refresh:after {
	content: "Loading..";
}

.card.card-act-fullscreen {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 1040;
	margin-bottom: 0;
	overflow-y: auto;
	-webkit-overflow-scrolling: touch;
	backface-visibility: hidden;
}

.card.card-act-hidden .card-block {
	display: none;
}

/* Card block */
.card-block {
	padding: 24px 24px;
}

.card-block:first-child {
	border-top-right-radius: 3px;
	border-top-left-radius: 3px;
}

