	margin-top: 0;
}

.card-block > :last-child {
	margin-bottom: 0;
}

.card-header + .card-block {
	padding-top: 16px;
}

/* Card image */
.card-img {
	border-radius: 3px;
}

.card-img-overlay {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	padding: 1.25rem;
}

/* Card image caps */
.card-img-top {
	border-radius: 3px 3px 0 0;
}

.card-img-bottom {
	border-radius: 0 0 3px 3px;
}

/* Profile card (used in profile page) */
.card-profile-img {
	height: 240px;
}

.card-profile-block {
	position: relative;
}

