	background-color: transparent;
	border-bottom: 1px solid rgba(0, 20, 35, 0.038);
}

.card > .nav-tabs.nav-tabs-alt > li > a {
	margin-bottom: -1px;
	-webkit-transition: all 0.15s ease-out;
	-o-transition: all 0.15s ease-out;
	transition: all 0.15s ease-out;
}

